import common from "../../common/common.js"
var serverUrl = common.serverUrl
import HMmessages from "../../components/HM-messages/HMmessages";
export default{
	components:{
		HMmessages
	},
	data(){
		return{
			// userId:"",
			// password:""
			status:true,
			account:"",
			password:"",
			telephone:"",
			otpCode:"",
			codeTime:0,
			code:"",
			checked:true
		};
	},
	created(){
		var account = uni.getStorageSync("account")
		if(account!==null&&account!==""&&account!==undefined){
			this.account=account
		}
		var password = uni.getStorageSync("pwd")
		if(password!==null&&password!==""&&password!==undefined){
			this.password=password
		}
	},
	methods:{
		forgetPwd(){
			uni.navigateTo({
				url:"../meInfo/children/password"
			})
		},
		rememberMe(){
			this.checked=!this.checked
		},
		submitAp() {
			if (this.account === "") {
				this.warning("用户名/手机号不能为空哟")
				return
			}
			if (this.password === "") {
				this.warning("密码不能为空哟")
				return
			}
			if (this.validate2()) {
				uni.request({
					url:serverUrl+'/users/tpLogin',
					data:{
						"telephone":this.account,
						"password":this.password,
					},
					method:"POST",
					success: (res) => {
						// debugger
						if(res.data.code===0){
							var userInfo ={}
							userInfo=res.data.data
							// 缓存的API  保存用户信息到全局的缓存中
							uni.setStorageSync("globalUser",userInfo)
							if (this.checked) {
								uni.setStorageSync('account',this.account)
								uni.setStorageSync('pwd',this.password)
							}
							uni.navigateBack({delta:1});
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/xixi.png"
							})
						}else if(res.data.code===466){
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/error1.jpg"
							})
						}else if(res.data.code===1){
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/error1.jpg"
							})
						}
					}
				})
			}else {
				uni.request({
					url:serverUrl+'/users/upLogin',
					data:{
						"username":this.account,
						"password":this.password,
					},
					method:"POST",
					success: (res) => {
						// debugger
						if(res.data.code===0){
							var userInfo ={}
							userInfo=res.data.data
							// 缓存的API  保存用户信息到全局的缓存中
							uni.setStorageSync("globalUser",userInfo)
							if (this.checked) {
								uni.setStorageSync('account',this.account)
								uni.setStorageSync('pwd',this.password)
							}
							uni.navigateBack({delta:1})
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/xixi.png"
							})
						}else if(res.data.code===466){
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/error1.jpg"
							})
						}else if(res.data.code===1){
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/error1.jpg"
							})
						}
					}
				})
			}
		},
		submitTc() {
			if (this.telephone === "") {
				this.warning("手机号不能为空哟")
				return
			}
			if (this.otpCode === "") {
				this.warning("验证码不能为空哟")
				return
			}
			if (this.validate()) {
				uni.request({
					url:serverUrl+'/users/otpLogin',
					data:{
						"telephone":this.telephone,
						"code":this.otpCode,
					},
					method:"POST",
					success: (res) => {
						// debugger
						if(res.data.code===0){
							var userInfo ={}
							userInfo=res.data.data
							// 缓存的API  保存用户信息到全局的缓存中
							uni.setStorageSync("globalUser",userInfo)
							uni.navigateBack({delta:1})
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/xixi.png"
							})
						}else if(res.data.code===466){
							uni.showToast({
								title:res.data.msg,
								duration:2000,
								image:"../../static/icos/error1.jpg"
							})
						}
					}
				})
			}
		},
		successHandler(){
			var userInfo ={}
			userInfo=res.data.data
			// 缓存的API  保存用户信息到全局的缓存中
			uni.setStorageSync("globalUser",userInfo)
			uni.navigateBack({delta:1})
			uni.showToast({
				title:res.data.msg,
				duration:2000,
				image:"../../static/icos/xixi.png"
			})
		},
		getOtpCode(){
			var telephone = this.telephone
					if (telephone === "") {
						this.warning("手机号不能为空哟")
						return
					}
					if(this.codeTime>0){
						this.warning("少侠手速太快了！")
						return
						}
						uni.request({
							url:serverUrl+'/users/otp/'+telephone+'?type=tp',
							method:'GET',
							success: (res) => {
								if(res.data.code===0){
									uni.showToast({
										title:res.data.data,
										mask:true,
										duration:2000
									})
									this.codeTime=30
									let timer = setInterval(()=>{
										if(this.codeTime>=1){
											this.codeTime--
										}else{
											this.codeTime=0
											clearInterval(timer)
										}
									},1000)
								}
								if (res.data.code === 10009) {
									uni.showToast({
										title:res.data.msg,
										mask:true,
										duration:2000
									})
								}else if (res.data.code === 10008) {
									uni.showToast({
										title:res.data.msg,
										mask:true,
										duration:2000
									})
								}else if (res.data.code === 10019) {
									uni.showToast({
										title:res.data.msg,
										mask:true,
										duration:2000
									})
								}
							}
						});

				},
		appOAuthLogin(e){
			var logintype =e.currentTarget.dataset.logintype
			uni.login({
				provider:logintype,
				success: (loginRes) => {
					// 授权登录成功以后 获取用户的信息
					uni.getUserInfo({
						provider:logintype,
						success: (info) => {
							var userInfo = info.userInfo
							var faceIcon = ""
							var nickName=""
							var thirdPartyId=""
							if(logintype=="weixin"){
								faceIcon = userInfo.avatarUrl
								nickName=userInfo.nickName
								thirdPartyId=userInfo.openId
							}else if(logintype=="qq"){
								 faceIcon = userInfo.figureurl_qq_2
								 nickName=userInfo.nickname
								 thirdPartyId=userInfo.openid
							}else if(logintype=="sinaweibo"){
								 faceIcon = userInfo.avatar_large
								 nickName=userInfo.nickName
								 thirdPartyId=userInfo.id
							}
							uni.request({
								url:serverUrl+'/users/thirdPartyLogin/'+logintype,
								data:{
									"faceIcon":faceIcon,
									"nickName": nickName,
									"thirdPartyId": thirdPartyId
								},
								method:"POST",
								success: (res) => {
									if(res.data.code==0){
									var userInfo ={}
										userInfo=res.data.data
										// 缓存的API  保存用户信息到全局的缓存中
										uni.setStorageSync("globalUser",userInfo)
										uni.switchTab({
											url:"../me/me"
										})
										uni.showToast({
											title:res.data.msg,
											duration:2000,
											image:"../../static/icos/xixi.png"
										})
									}else if(res.data.code==466){
										uni.showToast({
											title:res.data.msg,
											duration:2000,
											image:"../../static/icos/error1.jpg"
										})
									}
								}

							})
						}
					})
				}
			})
			var appOAuthLogin
		}
		,
		//实现在微信小程序的微信登录
		wxLogin(e){
				// 通过微信开放能力 获得微信用户的基本信息
				var userInfo = e.detail.userInfo
				//实现微信登录
				uni.login({
					provider:"weixin",
					success: (loginResult) => {
					// 获得微信登录的code:授权码
					var code = loginResult.code
					uni.request({
						url:serverUrl+'/users/mpLogin/'+code,
						data:{
							"avatarUrl":userInfo.avatarUrl,
							"city": userInfo.city,
							"country": userInfo.country,
							"gender": userInfo.gender,
							"language": userInfo.country,
							"nickName": userInfo.nickName,
							"province": userInfo.province
						},
						method:"POST",
						success: (res) => {
							if(res.data.code===0){
							var userInfo ={}
								userInfo=res.data.data
								// 缓存的API  保存用户信息到全局的缓存中
								uni.setStorageSync("globalUser",userInfo)
								uni.navigateBack({delta:1})
								uni.showToast({
									title:res.data.msg,
									duration:2000,
									image:"../../static/icos/xixi.png"
								})
							}else if(res.data.code==466){
								uni.showToast({
									title:res.data.msg,
									duration:2000,
									image:"../../static/icos/error1.jpg"
								})
							}
						}
					})
					}
				})
		},
		formSubmit(e){
			var userId = e.detail.value.userId
			var password = e.detail.value.password
			uni.request({
				url:serverUrl+'/users/registerOrLogin',
				data:{
					"userId":userId,
					"password":password
				},
				method:"POST",
				success: (res) => {
					if(res.data.code==0){
						var userInfo ={}
						userInfo=res.data.data
						// 缓存的API  保存用户信息到全局的缓存中
						uni.setStorageSync("globalUser",userInfo)
						uni.switchTab({
							url:"../me/me"
						})
					}else if(res.data.code==466){
						uni.showToast({
							title:res.data.msg,
							duration:2000,
							image:"../../static/icos/error1.jpg"
						})
					}

				}
			})
		},
		initForm(){
			this.username="",
			this.password="",
			this.telephone="",
			this.otpCode=""
		},
		changeLogin(){
			this.initForm()
			this.status=!this.status
		},
		validate(){
			var mPattern =/^1[34578]\d{9}$/
			if(!mPattern.test(this.telephone)){
				this.warning("手机号格式错误")
				return false
			}else{
				return true
			}
		},
		validate2(){
			var mPattern =/^1[34578]\d{9}$/
			if(!mPattern.test(this.account)){
				// this.warning("手机号格式错误")
				return false
			}else{
				return true
			}
		},
		warning(msg) {
			this.HMmessages.show(msg, {iconColor: '#ffffff', fontColor: '#ffffff', background: "#ffd655"})
		}
	},


}
